/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #525252;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 4px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px rgb(82, 82, 82), 0 0 5px rgb(36, 36, 36);
  opacity: 1;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root {
  --main-bg-color: #e7e7e7;
  --event-background-color: #f0f0f0;
  --main-color: #525252;
  --event-details-color: #858585;
  --event-description-color: #5c5c5c;
}

body,
html {
  font-family: 'Roboto', sans-serif;
  background-color: #e7e7e7;
  background-color: var(--main-bg-color);
}

body {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

input,
nav textarea,
button,
select,
a {
  outline: none !important;
  box-shadow: none !important;
  -webkit-tap-highlight-color: transparent !important;
}

.App {
  height: 100%;
}

ul {
  list-style-type: none;
}

.navbar-collapse {
  max-width: 100%;
  /* overflow: hidden; */
}

.navbar-logo {
  width: 50px;
}

.inputFields {
  display: flex;
  width: 100%;
  /* align-items: flex-end; */
}

.inputFields div {
  flex-grow: 1;
  padding: 0 5px;
}

.displayed-selection {
  flex-wrap: wrap;
}

.dropdown-toggle {
  width: 100%;
  border: 1px solid #858585;
  border: 1px solid var(--event-details-color);
}

.dropdown-toggle:hover {
  border: 1px solid #858585;
  border: 1px solid var(--event-details-color);
}

.event-card-wrapper {
  margin: 1% 0;
}

.event-card {
  background-color: #f0f0f0;
  background-color: var(--event-background-color);
}

.event-card-body {
  border: 1px solid #9c9c9c;
  cursor: pointer;
}

.card-top {
  color: #525252;
  color: var(--main-color);
  display: flex;
  flex-flow: wrap column;
  justify-content: space-between;
  min-height: 120px !important;
  text-align: center;
}

.event-details {
  color: #858585;
  color: var(--event-details-color);
}

.card-top-left {
  flex-grow: 1;
}

.card-top-right {
  flex-grow: 1;
}

.event-description {
  margin: 5px;
  color: #5c5c5c;
  color: var(--event-description-color);
}

.logout-button,
.portfolio-link {
  background-color: transparent;
  color: #525252;
  color: var(--main-color);
  width: 150px;
  padding:2px !important;
  text-align: center;
  border: 1px solid #858585;
  border: 1px solid var(--event-details-color);
}

.logout-button:hover,
.portfolio-link:hover {
  background-color: #525252;
  background-color: var(--main-color);
  color: #f0f0f0;
  color: var(--event-background-color);
  border: none;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
  input[type='range'] {
    border: 1px solid #525252;
    border: 1px solid var(--main-color);
    overflow: hidden;
    width: 100%;
    -webkit-appearance: none;
    background-color: transparent;
  }

  input[type='range']:focus {
    border: 1px solid #858585;
    border: 1px solid var(--event-details-color);
    background-color: transparent;
  }

  input[type='range']::-webkit-slider-runnable-track {
    height: 10px;
    -webkit-appearance: none;
    color: --main-bg-color;
    margin-top: -1px;
  }

  input[type='range']::-webkit-slider-thumb {
    width: 10px;
    -webkit-appearance: none;
    height: 10px;
    cursor: ew-resize;
    background: #525252;
    background: var(--main-color);
    box-shadow: none;
  }

  .dropdown-item:focus {
    background-color: transparent;
    color: #525252;
    color: var(--main-color);
  }

  .dropdown-menu {
    background-color: #e7e7e7;
    background-color: var(--main-bg-color);
  }
}

.Alert {
  position: absolute;
  text-align: center;
  top: 5px;
}

